div.table-title {
	display: block;
	max-width: 300px;
	padding:0px;
	width: 100%;
}

.table-title h3 {
   color: #413d3d;
   font-size: 15px;
   font-weight: 400;
   height: 25px;
   font-style:normal;
   font-family: "Roboto", helvetica, arial, sans-serif;
   text-shadow: -1px -1px 1px rgba(0, 0, 0, 0.1);
   text-transform:uppercase;
   text-align: center;
}


/*** Table Styles **/

.table-fill {
  background: rgb(22, 21, 21);
  border-radius:none;
  border-collapse: collapse;
  height: 100px;
  margin-left: auto;
  margin-right: auto;
  max-width: 100px;
  padding:5px;
  width: 100%;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
  -webkit-animation: float 5s infinite;
          animation: float 5s infinite;
  text-align: center;
}
 
th {
  color:#D5DDE5;;
  background:#333944;
  border-bottom:none solid #9ea7af;
  border-right: none solid #343a45;
  font-size:13px;
  font-weight: 100;
  padding:10px;
  text-align:center;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
  vertical-align:middle;
  margin:0;
}

th:first-child {
  border-top-left-radius:none;
}
 
th:last-child {
  border-top-right-radius:none;
  border-right:none;
}
  
tr {
  border-top: none solid #C1C3D1;
  border-bottom: none solid #C1C3D1;
  color:#666B85;
  font-size:16px;
  font-weight:normal;
  text-shadow: 0 1px 1px rgba(256, 256, 256, 0.1);
}
 
tr:hover td {
  background:#4E5066;
  color:#FFFFFF;
  border-top: none solid #22262e;
}
 
tr:first-child {
  border-top:none;
}

tr:last-child {
  border-bottom:none;
}
 
tr:nth-child(odd) td {
  background:#EBEBEB;
}
 
tr:nth-child(odd):hover td {
  background:#4E5066;
}

tr:last-child td:first-child {
  border-bottom-left-radius:none;
}
 
tr:last-child td:last-child {
  border-bottom-right-radius:none;
}
 
td {
  background:#FFFFFF;
  padding:25px;
  text-align: center;
  vertical-align:middle;
  font-weight:300;
  font-size:15px;
  text-shadow: -1px -1px 1px rgba(0, 0, 0, 0.1);
  border-right: none solid #C1C3D1;
}

td:last-child {
  border-right: none;
}

th.text-left {
  text-align: left;
}

th.text-center {
  text-align: center;
}

th.text-right {
  text-align: right;
}

td.text-left {
  text-align: left;
}

td.text-center {
  text-align: center;
}

td.text-right {
  text-align: right;
}

div.table-subtitle {
	display: block;
	margin: 0;
	max-width: 300px;
	padding:5px;
	width: 100%;
}

.table-subtitle h4 {
   color: #413d3d;
   font-size: 10px;
   font-weight: 400;
   height: 25px;
   font-style:normal;
   text-shadow: -1px -1px 1px rgba(0, 0, 0, 0.1);
   text-transform:uppercase;
   text-align: center;
   margin:0;
}

.equal {
  font-size: 50px;
}
.ava {
  font-size: 15px;
}
